html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #F5F5F5;
}

a {
  text-decoration: none;
  color: #b3d82b;
}

p {
  font-family: "Open Sans", sans-serif;
}

.section-title {
  color: #5B2A84;
  font-weight: bold;
}

.section-link {
  font-size: 0.8rem;
  color: #231F20;
}
.section-link:hover {
  color: #b3d82b;
}

.news-tags {
  color: #411763;
}

.news-title {
  color: #231F20;
}

.news-date {
  color: #424242;
}

.calendar-date {
  color: #411763;
}

.calendar-month {
  color: #231F20;
}

.calendar-event {
  color: #411763;
}

.calendar-time {
  color: #424242;
}

.is-style-kings-file-block,
.editor-styles-wrapper .is-kings-file-block {
  background-color: #e5ebc3;
  padding: 0.8rem;
}
.is-style-kings-file-block a,
.editor-styles-wrapper .is-kings-file-block a {
  color: #231F20;
  font-weight: 500;
  padding-left: 1rem;
}
.is-style-kings-file-block a:hover,
.editor-styles-wrapper .is-kings-file-block a:hover {
  color: #5B2A84;
}
.is-style-kings-file-block a::before,
.editor-styles-wrapper .is-kings-file-block a::before {
  font: var(--fa-font-solid);
  content: "\f15b";
  font-size: 1rem;
  color: #424242;
  border-bottom: #F5F5F5 1px solid;
  padding-right: 1rem;
}